<template>
  <div class="breakpoint-upload">
    <progress v-show="isShowProgress" :value="percent" :max="100"></progress><span>{{ percent }}%</span>
    <p v-if="file">{{ file.name }}<a v-if="file.url" :href="file.url" style="margin-left: 20px"></a></p>
    <Button :disabled="percent>0&&percent<100" class="breakpoint-btn" @click="triggerFile"></Button>
    <input id="breakpoint-file" type="file" @change="onchange($event)">
    <Button v-if="isShowSuspendBtn" class="suspend-btn" @click="suspend"></Button>
    <Button v-if="isShowContinueBtn" class="continue-btn" @click="goOn"></Button>
    <Button v-if="isShowCancelBtn" class="cancel-btn" @click="cancel"></Button>
  </div>
</template>
<script>
import { md5File } from '../utils/md5'

export default {
  name: 'PkUpload',
  props: {
    /* 500M */
    maxSize: {
      type: Number,
      default() {
        return 500 * 1024 * 1024
      }
    },
    defaultFile: {
      type: Object,
      default() {
        return {}
      }
    },
    uploadUrl: {
      type: String,
      default: '',
      required: true
    },
    md5: {
      type: String,
      default: ''
    },
    needMd5: {
      type: Boolean,
      default: false
    },
    fileType: {
      type: [String, Array],
      default: ''
    }
  },
  data() {
    return {
      ws: null,
      reader: null, // 
      step: 1024 * 64, // 
      cuLoaded: 0, // 
      file: this.defaultFile, // 
      enableRead: true, // 
      total: 0, // 
      startTime: null, // 
      isShowProgress: false,
      percent: 0,
      isShowCancelBtn: false,
      isShowSuspendBtn: false,
      isShowContinueBtn: false,
      fileTarget: null,
      currentMd5: this.md5,
      lockReconnect: false, // ws
      timeout: 1000, // 1
      timeoutObj: null,
      serverTimeoutObj: null,
    }
  },
  watch: {
    percent(newVal) {
      if (newVal > 0 && newVal < 100) {
        this.isShowCancelBtn = true
        this.isShowSuspendBtn = true
      } else if (newVal >= 100) {
        this.isShowSuspendBtn = false
      }
    }
  },
  beforeDestroy() {
    this.reset()
    this.suspend()
  },
  mounted() {
    try {
      if (!this.uploadUrl) {
        alert('')
      } else {
        this.createWebSocket()
      }
    } catch (e) {
      this.reconnect()
    }
    // websocketserver
    window.onbeforeunload = () => {
      this.ws.close()
    }
  },
  methods: {
    triggerFile() {
      document.querySelector('#breakpoint-file').click()
    },
    // WebSocket 
    createWebSocket(callback) {
      this.ws = new WebSocket(this.uploadUrl)
      /*  */
      this.ws.onopen = () => {
        callback && callback()
      }
      this.ws.onmessage = e => {
        const data = e.data
        this.reset()
        this.start()
        if (isNaN(data) === false) {
          this.isShowProgress = true
          this.cuLoaded = parseInt(data)
          this.showProcess()
          this.bindReader()
        } else if (data === 'canceled') {
          this.cuLoaded = 0
          this.percent = 0
          this.showProcess()
          this.enableRead = false
          this.reader.abort()
          this.isShowContinueBtn = this.isShowSuspendBtn = this.isShowCancelBtn = false
          this.reset()
        } else if (data === 'ok') {
          if (this.cuLoaded < this.total) {
            this.readBlob()
          } else {
            this.ws.send('act:complete')
          }
          this.showProcess()
        } else if (data !== 'ping') {
          /*  */
          this.$emit('onSuccess', data, this.currentMd5, this.file.size)
          this.reset()
        }
      }
      this.ws.onclose = e => {
        console.log(e)
        this.reconnect()
      }
      this.ws.onerror = e => {
        this.reconnect()
        console.error(e)
        this.$Message.error('')
      }
    },
    // 
    showProcess() {
      const percent = ((this.cuLoaded / this.total) * 100)
      this.percent = parseInt(percent.toFixed(0))
      this.$emit('getPercent', percent.toFixed(2) + '%')
    },
    // reader
    bindReader() {
      this.startTime = new Date()
      this.enableRead = true
      this.reader = new FileReader()
      // 
      this.reader.onload = (e) => {
        if (this.enableRead === false) {
          return false
        }
        // 
        if (this.ws.bufferedAmount > this.step * 10) {
          setTimeout(() => {
            // 
            this.loadSuccess(e.loaded)
          }, 3)
        } else {
          // 
          this.loadSuccess(e.loaded)
        }
      }
      // 
      this.readBlob()
    },
    onchange(e) {
      this.fileTarget = e.target
      const getFileType = (value) => {
        value = value.split('.')
        return value[value.length - 1]
      }
      const fileType = getFileType((this.fileTarget.value))
      if (!this.fileType || (this.fileType && this.fileType.includes(fileType))) {
        // 
        const file = (event.target.files || event.dataTransfer.files)[0]
        /*  */
        if (file && file.size > this.maxSize) {
          return this.$emit('overSize')
        }
        this.file = file
        if (this.file) {
          this.total = this.file.size
        }
        const startSend = () => {
          if (this.ws == null) {
            this.$Modal.confirm({
              title: '',
              content: ',',
              onOk: () => {
                this.createWebSocket(this.sendFileName)
              }
            })
            return
          }
          this.percent = 0
          this.sendFileName()
        }
        if (this.needMd5) {
          md5File(file, (md5) => {
            this.currentMd5 = md5
            if (this.md5 === md5) {
              this.percent = 100
              this.$emit('getPercent', '100%')
            } else {
              startSend()
            }
          })
        } else {
          startSend()
        }
      } else {
        this.$Message.error('')
      }
    },
    // 
    readBlob() {
      // 
      if (this.file) {
        const blob = this.file.slice(this.cuLoaded, this.cuLoaded + this.step)
        this.reader.readAsArrayBuffer(blob)
      }
    },
    sendFileName() {
      this.$emit('getFileName', this.file.name)
      this.ws.send('fileName:' + this.file.name)
    },
    // 
    loadSuccess(loaded) {
      // 
      const blob = this.reader.result
      // WebSocket 
      this.ws.send(blob)
      // 
      this.cuLoaded += loaded
    },
    /**
       * 
       */
    suspend() {
      // 
      this.isShowSuspendBtn = false
      this.isShowContinueBtn = true
      this.enableRead = false
      this.reader && this.reader.abort()
    },
    // 
    goOn() {
      this.isShowSuspendBtn = true
      this.isShowContinueBtn = false
      this.enableRead = true
      this.readBlob()
    },
    /**
       * 
       */
    cancel() {
      this.fileTarget.value = null
      this.file = null
      this.isShowProgress = false
      this.$emit('cancelUpload')
      if (this.ws) {
        this.ws.send('act:cancel')
      }
    },
    /**
       * 
       */
    reconnect() {
      if (this.lockReconnect) return
      this.lockReconnect = true
      setTimeout(() => { // 
        this.createWebSocket((this.percent < 100 && this.percent > 0) && this.sendFileName)
        this.lockReconnect = false
      }, 2000)
    },
    reset() {
      clearTimeout(this.timeoutObj)
      clearTimeout(this.serverTimeoutObj)
      return this
    },
    start() {
      this.timeoutObj = setTimeout(() => {
        // 
        // onmessage
        this.ws.send('act:ping')
        this.serverTimeoutObj = setTimeout(() => { // 
          this.ws.close() // onclosereconnectws.close().reconnect onclose
        }, this.timeout)
      }, this.timeout)
    }
  },
}
</script>
<style scoped>
  @import "../lib/css/index.css";
</style>
